/* App.css */
.todayDiscovery{
  width:980px;
  margin:10px auto 30px;
}
h2{display:inline-block}
small::before{
  content:'|';
  margin:0 10px;
}
div{
  display:flex;
  justify-content:center;
  flex-wrap:wrap;
  margin:0;
  padding:5px;
  border:1px solid #ccc;
}
img{
  margin:5px;
  border:1px solid #ccc;
  z-index:1;
  pointer-events: none;
}
figure{
  margin:0;
  position: relative;
  z-index: 10;
}
figcaption::before{
  color:#fff;
  content:'>';
  background:rgba(150,150,150,0.5);
  width:40px;
  height:32px;
  padding-top:8px;
  display: flex;
  justify-content:center;
}
figcaption{
  position:absolute;
  left:25px; bottom:25px;
}
button{
  height:40px;
  border:none;
  color:#fff;
  display:none;
  background:#f00;
  position: absolute;
  left:0; top:0;
}
figure:hover button{
  display: block;
  width:100px;
}
figure:hover img{
  border:1px solid #f00;
}
@media(max-width:1000px){
  .todayDiscovery{width:500px;}
}

@media(max-width:600px){
  .todayDiscovery{
    width:80vw;
  }
  .todayDiscovery figure{width:80vw;}
  .todayDiscovery figure img{width:78vw;}
}

@media(max-width:500px){
  .todayDiscovery figure img{width:75vw;}
}
